<script>
import Header from "../components/Header";
import Footer from "../components/Footer";

export default {
  components: {Footer, Header},
  data() {
    return {
      isLike: false,
      course_cat:[],
      courses:[]
    }
  },
  mounted() {
    this.$axiosDefault.get('/course-categories/',{
      headers:{
        'Accept-Language':localStorage.getItem('lang')
      }
    })
    .then(res=>{
      this.course_cat=res.data
    })
    this.$axiosDefault.get('/courses/',{
      headers:{
        'Accept-Language':localStorage.getItem('lang')
      }
    })
        .then(res=>{
          this.courses=res.data
        })
  },
  methods:{
    getFilt(item){
      this.$axiosDefault.get(`/courses/?num_lessons=${item}`,{
      headers:{
        'Accept-Language':localStorage.getItem('lang')
      }
    })
        .then(res=>{
          this.courses=res.data
        })
    },
    getFiltCat(item){
      this.$axiosDefault.get(`/courses/?category_id=${item}`,{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
      })
          .then(res=>{
            this.courses=res.data
          })
    },
    Likelist(item){
      this.$axiosDefault.post(`/courses/${item.id}/add_to_wishlist/`,{
            "id": item.id,
          }
          ,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            }
          })
          .then(()=>{

            var k=document.getElementById(`index${item.id}`)
            k.classList.toggle(`active`)
          })
      .catch(error=>{
        console.log(error.request.status)
        if(error.request.status==401){
          this.$toast(`${error.message}`, {
            timeout: 2000,
            type:"error"
          });
        }
      })
    }
  }
}
</script>
